import React, { Component } from "react";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { connect } from "react-redux";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class AccordRongsok extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentDidMount() {
    this.props.change("rongsokBruto", this.props.totalBrutoRongsok);
    this.props.change("rongsokNetto", this.props.totalRongsok);
  }
  setRongsok() {
    this.props.change("rongsokBruto", this.props.totalBrutoRongsok);
    this.props.change("rongsokNetto", this.props.totalRongsok);
  }
  render() {
    return (
      <div className="col-lg-6">
        <div id="accordion" className="custom-accordion mb-4">
          <div className="card mb-0 bg-dark">
            <div className="card-header" id="headingOne">
              <div className="row">
                <div className="col-lg-8 col-md-8">
                  <h5 className="m-0">
                    <a
                      className="custom-accordion-title d-block pt-2 pb-2 text-white"
                      data-toggle="collapse"
                      href="#collapseRongsok"
                      aria-expanded="true"
                      aria-controls="collapseRongsok"
                    >
                      Rongsok
                      <span className="float-right">
                        <i className="mdi mdi-chevron-down accordion-arrow" />
                      </span>
                    </a>
                  </h5>
                </div>
                <div className="col-md-4 col-lg-4">
                  <label htmlFor="" style={{ color: "white" }}>
                    Netto
                  </label>
                  <Field
                    name="rongsokNetto"
                    type="number"
                    component="input"
                    className="form-control"
                    readOnly={true}
                  />
                </div>
              </div>
            </div>
            <div
              id="collapseRongsok"
              className="collapse"
              aria-labelledby="headingOne"
              data-parent="#accordion"
            >
              <div className="card-body bg-white">
                <div className="row">
                  <div className="col-lg-2">
                    <label htmlFor="">.</label>
                    <button
                      className="btn btn-primary btn-block"
                      type="button"
                      data-target="#rongsokModal"
                      data-toggle="modal"
                    >
                      +
                    </button>
                  </div>
                  <div className="col-lg-5 col-md-5">
                    <Field
                      name="rongsokBruto"
                      label="Brutto"
                      type="number"
                      component={renderField}
                      readOnly={true}
                      onBlur={this.setRongsok()}
                    />
                  </div>
                  <div className="col-lg-5 col-md-5">
                    <Field
                      name="rongsokNetto"
                      label="Netto"
                      type="number"
                      component={renderField}
                      readOnly={true}
                      onBlur={this.setRongsok()}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>{" "}
          {/* end card*/}
        </div>
      </div>
    );
  }
}
AccordRongsok = reduxForm({
  form: "AccordRongsok",
  enableReinitialize: true,
})(AccordRongsok);
const selector = formValueSelector("AccordRongsok"); // <-- same as form name
export default connect((state) => {
  const { harga, cash } = selector(state, "harga", "cash");
  return {
    cashNetto: (parseFloat(cash) / parseFloat(harga)).toFixed(3),
  };
})(AccordRongsok);
